<template>
  <div>
    <div class="navBar">
      <a href="javascript:;" class="nav-bar-back" @click="goBack"></a>
      <img class="navTitleImg" :src="'//cdn09.ehaier.com/shunguang/H5/www//img/hotSale/title.png' | imageToHttps" alt="" />
    </div>
    <every-day-list-top
      @changeCountItem="changeCountItem"
      @getCountItem="getCountItem"
      :countItem="countItem"
      @getCategoryId="getCategoryId"
      :activeIndex="activeIndex"
    ></every-day-list-top>
    <div id="productList" class="productList">
      <ul v-if="haveNews">
        <li @click="goProductDetailsPage(i)" v-for="(i, k) in list" :key="k">
          <img class="list_left" :src="i.imageUrl | imageToHttps" />
          <div class="list_right">
            <div class="title">{{ i.name }}</div>
            <div class="text">{{ i.title }}</div>
            <div class="priceAndCommission">
              <span class="price"><em>￥</em>{{ i.price }}</span>
              <div :class="isWdHost == 1 && $toggleSwitch.commissionDisplay ? 'earnCommission' : 'noShow'">赚</div>
              <span :class="isWdHost == 1 && $toggleSwitch.commissionDisplay ? 'commission' : 'noShow'">￥{{ i.commission }}</span>
            </div>
            <img v-if="k < 3" class="list_right_img" :src="countImg + (k + 1) + '.png' | imageToHttps" alt="" />
            <div class="rankingPictures" v-if="k > 2">
              <img :src="'//cdn09.ehaier.com/shunguang/H5/www/img/hotSale/4.png' | imageToHttps" alt="" />
              <span class="span"><span v-if="k <9">0</span>{{ k + 1 }}</span>
            </div>
          </div>
        </li>
      </ul>
      <div class="noNews" v-if="noNews">
        <div class="noNewsDiv">
          <img alt :src="`${$cdnImageUrl}/everyDayList/noRanking.png` | imageToHttps" />
          <div>暂无排行</div>
        </div>
      </div>
    </div>
    <div
      v-if="goNextList && nextName && nextName.length > 0 &&list.length > 0"
      class="bottomToNextPage"
      @touchend="touchEnd"
      @touchstart="touchStart">上滑进入{{ nextName }}热卖榜单</div>
  </div>
</template>

<script>
// import utils from '@/utils/utils'
import everyDayListTop from './components/every-day-list-top'
import { productList } from '@/http/service/every-day-list'
export default {
  name: 'everyDayList',
  data() {
    return {
      activeIndex: 0,
      countImg: 'http://cdn09.ehaier.com/shunguang/H5/www/img/hotSale/', // 排行榜图片
      categoryId: '', // 获取子集传递的值筛选后存储
      countItem: 0, // 给子集传递的索引
      list: [], // 存放数据
      noNews: false, // 没有数据就展示图片
      haveNews: true, // 控制有数据就展示每一列
      allHeight: '', // 总高度
      scrolltop: '', // 距离顶部的高度
      clientheight: '', // 可视区域的高度
      nextName: '', // 下一条的名称提示
      getName: '', // 存储下一条数据的名称
      productListTop: [], // 存放子集傳過來的數組
      goNextList: true, // 控制上滑下一条提示
    }
  },
  computed: {
    isWdHost() {
      // 存储微店主标识
      return this.$store.getters.getHasStore
    },
  },
  components: { everyDayListTop },
  methods: {
    goBack() {
      this.$store.dispatch('doHistoryBack')
    },
    /**
     * @description 跳转到商品详情页
     */
    goProductDetailsPage(e) {
      this.$router.push({
        path: './goodsDetail',
        query: {
          productId: e.id,
        },
      })
    },
    touchStart(e) {
      this.activeIndex++
      this.touchY = e.changedTouches[0].pageY;
    },
    touchEnd(e) {
      const disY = e.changedTouches[0].pageY - this.touchY;
      if (disY < -10 && this.goNextList) {
        if (this.countItem < this.productListTop.length - 1) {
          setTimeout(() => {
            this.countItem++;
          }, 500);
        }
      }
    },
    /**
     * @description 接收子组件的展开之后的每一条的点击事件
     * @param 子组件点击的索引
     */
    changeCountItem(e) {
      this.activeIndex = e
    },
    /**
     * @description 获取子组件未展开的每条点击事件，和子组件中监听的countItem的变化传值
     * @param 当前索引的数据 和下一条数据的关键字
     */
    getCountItem(e) {
      this.countItem = e.first.countId
      this.nextName = e.second
      this.getName = e.first.labelName
      this.changeProduct(this.getName)
    },
    /**
     * @description 封装 监听后数据请求的方法
     * @param 接收的name值
     */
    changeProduct(getName) {
      window.scrollTo(0, 0)
      switch (getName) {
        case '冰箱':
          this.categoryId = 8
          break
        case '热水器':
          this.categoryId = 11
          break
        case '空调':
          this.categoryId = 9
          break
        case '洗衣机':
          this.categoryId = 10
          break
        case '厨房电器':
          this.categoryId = 12
          break
        case '电视':
          this.categoryId = 13
          break
        case '冷柜':
          this.categoryId = 18
          break
        case '生活小电':
          this.categoryId = 69
          break
      }
      const a = {
        categoryId: this.categoryId,
        provinceId: this.$store.state.provinceId,
        cityId: this.$store.state.cityId,
        regionId: this.$store.state.districtId,
        streetId: this.$store.state.townshipId,
      }
      this.list = []
      productList(a).then(res => {
        this.list = res.data.data
        if (res.data.data === null) {
          this.noNews = true
          this.haveNews = false
          this.goNextList = false
        } else if (res.data.data.length < 1) {
          this.noNews = true
          this.haveNews = false
          this.goNextList = false
        } else {
          this.noNews = false
          this.haveNews = true
          this.goNextList = true
        }
      })
    },
    /**
     * @description 监听子组件传递的productListTop的数据
     * @params productListTop数组
     */
    getCategoryId(e) {
      this.productListTop = e
      this.nextName = e[1].labelName
      this.changeProduct(e[0].labelName)
    },
  },
  mounted() {
    // 判断接口开关是否显示佣金
    // if (this.$toggleSwitch.commissionDisplay) {
    //   isWdHost().then(res => {
    //     this.isWdHost = res.data.data.isHost
    //   })
    // }
    /**
     * @description 滚动条到达底部事件，把countItem++(索引值)
     */
    const context = this
    window.onscroll = function() {
      this.allHeight = document.documentElement.scrollHeight
      this.scrolltop = document.documentElement.scrollTop
      this.clientheight = document.documentElement.clientHeight
      // 判断到达底部
      if (context.list.length >= 1) {
        // 判断当前索引小于productListTop的length -1 的时候才执行索引++ 操作
        if (this.scrolltop + this.clientheight >= this.allHeight) {
          // // 判断商品列表有数据
          // if (context.countItem < context.productListTop.length - 1) {
          //   setTimeout(function() {
          //     context.countItem++
          //   }, 500)
          // } else {
          //   context.goNextList = false
          // }
          context.goNextList = true;
        }
      }
    }
    // }
  },
}
</script>

