import http from '../http'
import url from '../url'

export const productList = (params) => {
  return http.request({
    url: url.everyDayList,
    method: 'get',
    params
  })
}
export const everyDayListTopNews = (params) => {
  return http.request({
    url: url.everyDayListTop,
    method: 'get',
    params
  })
}
