<template>
  <div class="box">
    <div class="top" v-if="listClose">
      <div class="top_left" id="menu-top">
        <van-tabs v-model="active" color="#2e7eed">
          <van-tab @click="change(i, k, i + 1)" :id="`listUlLi${k}`" v-for="(i, k) in everyText" :key="k" :title="i.labelName"></van-tab>
        </van-tabs>
      </div>
      <div class='divid'></div>
      <div class="top_right" @click.stop="chooseList">
        <img  :src="`${$cdnImageUrl}/everyDayList/blueDown.png`" alt="" />
      </div>
    </div>
    <div class="top_open" v-if="listOpen" >
      <div class="top_open_top">
        <div>请选择类别:</div>
        <img @click="closeList" :src="`${$cdnImageUrl}/everyDayList/blueUp.png`" alt="" />
      </div>
      <div class="top_open_foot">
        <ul>
          <li @click="listActived(i, k)" v-for="(i, k) in everyText" :key="k" :class="['originalLi', selectId == k ? 'selectLiStyle' : '']">
            {{ i.labelName }}
          </li>
        </ul>
      </div>
    </div>
    <div v-if="show" @click="overlayClick" :style="{ height: Height }" class="overlayClick"></div>
  </div>
</template>

<script>
import { everyDayListTopNews } from '@/http/service/every-day-list'
export default {
  data() {
    return {
      textClass: false, // 控制list的点击样式
      selectId: 0, // 存储点击当前改变样式的id
      listClose: true, // 控制选项
      listOpen: false, // 控制选项的展开
      show: false, // 控制遮罩层
      windowHeight: '', // 存储屏幕的高度
      countId: 0, // 给获取到的属性都加一条countI属性
      everyText: [], // 存放数据
      marginLeft: 0, // 动态绑定的ul的marginleft
      listLi: '', // 存储每一个选项的距离，一个li标签的宽度
      touchStartX: '', // 存储手指按下的起始位置
      touchMoveX: '', // 存储移动中手指的位置
      listClickCount: 1, // 存储展开之后的list是否进行了点击操作
      listClickNum: 0, // 存储展开的list点击索引存储
      ulWidth: '',
      active: 0,
    }
  },
  props: ['countItem', 'activeIndex'],
  watch: {
    /**
     * @description 监听父级传递过来的索引
     */
    activeIndex(val) {
      this.active = val
    },
    /**
     * @description 监听索引改变样式切换，和传值
     */
    active(val) {
      this.selectId = val
      if (this.everyText.length > this.everyText.length - 1) {
        let nextName
        if (val === 7) {
          nextName = ''
        } else {
          nextName = this.everyText[val + 1].labelName
        }
        this.$emit('getCountItem', { first: this.everyText[val], second: nextName })
      }
      this.$emit('changeCountItem', val)
    },
  },
  computed: {
    /**
     * @description 计算遮罩层的高度
     */
    Height() {
      return (this.windowHeight) + 'px'
    },
  },
  created() {
    // 获取屏幕可视高度
    this.windowHeight = document.body.clientHeight
  },
  methods: {
    /**
     * @description top_right下的图片的点击事件（展开选项）
     */
    chooseList() {
      this.listClose = false
      this.listOpen = true
      this.show = true
    },
    /**
     * @description top_open_foot下的的图片的点击事件(关闭选项)
     */
    closeList() {
      this.listClose = true
      this.listOpen = false
      this.show = false
    },
    /**
     * @description 展开的list的点击更改当前样式
     */
    listActived(i, k) {
      this.$emit('changeCountItem', i.countId)
      this.listOpen = false
      this.listClose = true
      this.show = false
    },
    /**
     * @description 遮罩层的点击事件
     */
    overlayClick() {
      this.listOpen = false
      this.listClose = true
      this.show = false
    },
  },
  mounted() {
    /**
     * @description 获取头部数据 把关键词传递给父级
     */
    everyDayListTopNews({ type: 1 }).then(res => {
      for (let i = 0; i < res.data.data.length; i++) {
        res.data.data[i].countId = this.countId
        this.countId++
      }
      this.everyText = res.data.data
      this.$emit('getCategoryId', res.data.data)
    })
  },
}
</script>

